.cart {
    padding: 2rem 0;
  }
  
  .cart h1 {
    text-align: center;
    font-size: 3rem;
    color: var(--color-secundario);
  }
  
  .cart ul {
    width: 90%;
    margin: auto;
  }
  
  .cart ul li {
    display: flex;
    align-items: center;
  }
  
  .cart ul li img {
    width: 200px;
  }
  
  .cart ul li div {
    margin-left: 3rem;
  }
  
  .cart ul li h2 {
    color: var(--color-principal);
    font-size: 1.8rem;
  }
  
  .cart ul li p {
    font-size: 1.4rem;
  }
  
  .cart .final {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .cart .final p {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1rem;
  }
  
  .cart .final a {
    display: inline-block;
    background: var(--color-principal);
    padding: 0.5rem 1rem;
    font-size: 1.8rem;
    color: var(--blanco);
  }