@import url(https://fonts.googleapis.com/css2?family=East+Sea+Dokdo&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color-principal: lightblue;
  --color-secundario: #a13ddf;
  --blanco: #fff;
  --text: #333;
}

html {
  font-family: "Poppins", sans-serif;
  font-size: 10px;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

nav {
  background: var(--color-principal);
  padding: 2rem 0;
}

nav .container {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  letter-spacing: 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

nav h1 {
  font-family: 'East Sea Dokdo', cursive;
  font-size: 6rem;
  letter-spacing: 0.2rem;
  margin-bottom: 0.5rem;
}

p {
  font-size: 13px;
  margin-bottom: 2rem;
}

nav h1 a {
  color: black;
}

nav ul {
  display: flex;
}

nav ul li {
  margin: 0 2rem;
}

nav ul li:first-child {
  margin-left: 0;
}

nav ul li:last-child {
  margin-right: 0;
}

nav ul li a {
  color: var(--blanco);
  font-size: 1.5rem;
  position: relative;
}

nav ul li a::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.2rem;
  background: var(--blanco);
  position: absolute;
  bottom: -0.5rem;
  opacity: 0;
  transition: opacity 0.2s;
}

nav ul li a:hover::after {
  opacity: 1;
}

.navCart {
  position: absolute;
  right: 0;
  bottom: 0;
  cursor: pointer;
}

.navCart svg {
  color: var(--blanco);
  font-size: 2.8rem;
}

.navCart span {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 1.9rem;
  height: 1.8rem;
  font-size: 1.1rem;
  position: absolute;
  right: -2rem;
  top: -0.8rem;
  background: rgb(231, 3, 3);
  line-height: 0;
  color: var(--blanco);
}

.widgetCart {
  height: 100vh;
  width: 30%;
  position: fixed;
  right: -30%;
  top: 0;
  background: var(--blanco);
  z-index: 2;
  box-shadow: -0.3rem 0 0.3rem rgba(0, 0, 0, 0.2);
  transition: right 0.5s;
}

.close {
  right: -30%;
}

.open {
  right: 0;
}

.productCard {
  background: var(--blanco);
  border-radius: 1.5rem;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.5s;
  height: 100%;
  position: relative;
}

.productCard:hover {
  box-shadow: 0 0 0.8rem rgba(0, 0, 0, 0.25);
}

.productCard img {
  width: 100%;
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
}

.productCard div {
  padding: 1rem;
  text-align: center;
}

.productCard div h3 {
  color: var(--text);
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  min-height: 46px;
}

.productCard div p {
  color: var(--text);
  font-size: 2rem;
  font-weight: 600;
}

.productCard div a {
  display: block;
  border: none;
  padding: 0.5rem;
  outline: none;
  cursor: pointer;
  background: var(--color-principal);
  border-radius: 1rem;
  color: var(--blanco);
  font-size: 1.3rem;
  margin-top: 1rem;
  transition: background 0.5s;
}

.productCard div a:hover {
  background: #0758af;
}

.productCard.list {
  display: flex;
  align-items: center;
}

.productCard.list img {
  width: 20%;
  border-bottom-left-radius: 1.5rem;
  border-top-right-radius: 0;
}

.productCard.list div {
  display: grid;
  align-items: center;
  grid-template-columns: 3fr 1fr 2fr;
  flex: 1 1;
}

.productCard.list div h3 {
  margin: 0;
  min-height: auto;
  text-align: left;
  font-size: 2rem;
}

.featuredProducts {
  padding: 4rem 0;
}

.featuredProducts h2 {
  color: var(--color-secundario);
  text-align: center;
  margin-bottom: 3rem;
  font-size: 2.5rem;
}

.featuredProducts ul {
  display: flex;
  justify-content: space-between;
}

.featuredProducts ul li {
  width: 20%;
}

.cargando {
  text-align: center;
  font-size: 3rem;
}

.slider_img {
  width: 100%;
  height: 500px;
}

.slick-slide {
  outline: none;
}

.slider .slick-dots {
  bottom: 1.5rem;
}

.slider .slick-dots li {
  width: auto;
  height: auto;
  margin: 0 1rem;
}

.slider .slick-dots li button {
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
}

.slider .slick-dots li.slick-active button {
  background: var(--blanco);
}

.slider .slick-dots li button:before {
  display: none;
}

.newsletter {
  background: var(--text);
  padding: 4rem 0;
}

.newsletter h2 {
  text-align: center;
  color: var(--blanco);
  font-size: 2.5rem;
}

.newsletter p {
  text-align: center;
  color: var(--blanco);
  font-size: 1.4rem;
}

.newsletter form {
  width: 60%;
  margin: 2rem auto 0;
  display: flex;
  border-radius: 1.5rem;
  overflow: hidden;
}

.newsletter form input {
  width: 70%;
  padding: 1.5rem;
  border: none;
  font-size: 1.5rem;
  outline: none;
}

.newsletter form input[type="submit"] {
  width: 30%;
  background: var(--color-principal);
  color: var(--blanco);
  text-transform: uppercase;
  cursor: pointer;
  transition: background 0.5s;
}

.newsletter form input[type="submit"]:hover {
  background: #0758af;
}

.products {
  width: 100%;
}

.products > div {
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 2rem;
}

.products > div svg {
  font-size: 2.5rem;
  color: var(--color-principal);
}

.products_list.grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 2rem;
}

.products_list.list {
  display: grid;
  grid-template-columns: 100%;
  grid-gap: 2rem;
}


.category {
  min-height: 100vh;
  padding: 4rem;
}

.category h2 {
  text-align: center;
  font-size: 3rem;
  color: var(--color-secundario);
}

.category h2::first-letter {
  text-transform: uppercase;
}

.product {
  display: grid;
  grid-template-columns: 40% 1fr;
  grid-gap: 3rem;
}

.product .foto img {
  width: 100%;
}

.product .info .title {
  font-size: 3rem;
  color: var(--color-secundario);
  margin-bottom: 1.5rem;
}

.product .info .description {
  font-size: 1.4rem;
  color: var(--text);
  margin-bottom: 2rem;
}

.product .info .price {
  font-size: 2.5rem;
  font-weight: 600;
  color: var(--text);
  margin-bottom: 2rem;
}

.product .qty {
  width: 150px;
  margin-bottom: 2rem;
  display: flex;
}

.product .qty button {
  width: 20%;
  border: none;
  padding: 0.5rem;
  outline: none;
  cursor: pointer;
  background: var(--color-principal);
  border-radius: 1rem;
  color: var(--blanco);
  font-size: 1.5rem;
}

.product .qty button:disabled {
  background: #ddd;
  cursor: no-drop;
}

.product .qty input {
  width: 60%;
  text-align: center;
  border: none;
  padding: 0.5rem;
  text-align: center;
  font-size: 1.8rem;
  outline: none;
}

.product .btn {
  width: 150px;
  display: block;
  border: none;
  padding: 1rem;
  outline: none;
  cursor: pointer;
  background: var(--color-principal);
  border-radius: 1rem;
  color: var(--blanco);
  font-size: 1.5rem;
  margin-top: 1rem;
  transition: background 0.5s;
}

.product .btn:hover {
  background: #0758af;
}

.contador {
  align-items: center;
  display:flex;
}

.contador button {
  padding: 5px;
  margin: 0 10px 0;
}

.contador p {
  padding: 5px;
}
.breadcrum {
  margin: 2rem 0;
  display: flex;
}

.breadcrum li {
  text-transform: lowercase;
  font-size: 1.1rem;
  color: var(--text);
}

.breadcrum li::first-letter {
  text-transform: uppercase;
}

.breadcrum li:first-child::after {
  content: ">";
  display: inline-block;
  margin: 0 1rem;
}

.breadcrum li a {
  color: var(--text);
  text-decoration: underline;
}

.cart {
    padding: 2rem 0;
  }
  
  .cart h1 {
    text-align: center;
    font-size: 3rem;
    color: var(--color-secundario);
  }
  
  .cart ul {
    width: 90%;
    margin: auto;
  }
  
  .cart ul li {
    display: flex;
    align-items: center;
  }
  
  .cart ul li img {
    width: 200px;
  }
  
  .cart ul li div {
    margin-left: 3rem;
  }
  
  .cart ul li h2 {
    color: var(--color-principal);
    font-size: 1.8rem;
  }
  
  .cart ul li p {
    font-size: 1.4rem;
  }
  
  .cart .final {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .cart .final p {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1rem;
  }
  
  .cart .final a {
    display: inline-block;
    background: var(--color-principal);
    padding: 0.5rem 1rem;
    font-size: 1.8rem;
    color: var(--blanco);
  }
.checkout {
    padding: 2rem 0;
  }
  
  .checkout h2 {
    text-align: center;
    font-size: 3rem;
    color: var(--color-secundario);
  }
  
  .checkout form {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 50%;
  }
  
  .checkout form input,
  .checkout form button {
    padding: 1rem;
    margin-bottom: 1rem;
  }
